}

.l-grid:before, .l-grid:after {
  content: " ";
  display: table;
}

.l-grid:after {
  clear: both;
}

.l-grid--flex {
  display: flex;
  flex-wrap: wrap;
  /**
    		 * 1. Remove clearfix
         *    that provide bug on safari
    		 */
}

.l-grid--flex:before, .l-grid--flex:after {
  display: flex;
  /* 1 */
}

